* {
  box-sizing: border-box;
}

ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.btn {
  display: inline-block;
  border: 1px solid #ccc;
  text-decoration: none;
  padding: 5px 1rem;
  font-size: small;
  box-sizing: border-box;
}

.btn.primary {
  background-color: black;
  color: #fff;
}

.container {
  width: 90%;
  padding: 10px 20px;
  margin: 0 auto;
  background: #efefef;
}

.itemList {
  display: flex;
}

.itemList .item img {
  width: 100%;
}

nav ul li {
  display: inline-block;
  margin-right: 2rem;
}

.cartList li {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.cartList li img {
  width: 120px;
}

.cart .btn {
  margin-right: 5px;
}

.totalPrice {
  font-size: 1.2rem;
}
.totalPrice p span {
  margin-left: 1rem;
}
